<template>
  <div class="login-container">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px" class="login-form">
      <h2 class="login-title"></h2>
      <el-form-item label="" prop="userName">
        <el-input v-model="form.userName" type='text' placeholder=''></el-input>
      </el-form-item>
      <el-form-item label="" prop='password'>
        <el-input v-model="form.password" type='password' placeholder=''></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('form')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { loginReq } from '@/apis/login'

export default {
  data() {
    return {
      form: {
        userName: '',
        password: ''
      },
      rules: {
        userName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3-10', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: '3-10', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          loginReq(this.form.userName, this.form.password).then((res) => {
            console.log(res.data)
            if (res.data['status'] === '0') {
              this.$message.error('')
            } else {
              // 
              sessionStorage.username = this.userName
              sessionStorage.setItem('uuid', res.data['uuid'])
              this.$router.push('/main')
            }
          })
            .catch((err) => {
              this.$message.error('')
              console.error('faild', err)
            })
        } else {
          this.$message.error('')
          return false
        }
      })
    }
  }
}
</script>

<style acoped>
.login-form {
  width: 350px;
  margin: 160px auto; /* 160px*/
  background-color: rgb(255, 255, 255, 0.8); /*  */
  padding: 30px;
  border-radius: 20px; /*  */
}

/*  */
.login-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../../assets/login.jpg");
}

/*  */
.login-title {
  color: #303133;
  text-align: center;
}
</style>-->



